/* 清除内外边距 */


/**重置默认样式*/

body,
h1,
h2,
h3,
h4,
h5,
h6,
hr,
p,
blockquote,

/* structural elements 结构元素 */

dl,
dt,
dd,
ul,
ol,
li,

/* list elements 列表元素 */

pre,

/* text formatting elements 文本格式元素 */

fieldset,
lengend,
button,
input,
textarea,

/* form elements 表单元素 */

th,
td,
div {
    /* table elements 表格元素 */
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
}


/* 设置默认字体 */

body,
html {
    font-family: "微软雅黑", Helvetica, "Droid Sans", Arial, sans-serif;
    /* 用 ascii 字符表示，使得在任何编码下都无问题 */
    max-width: 640px;
    margin: 0 auto;
    -webkit-overflow-scrolling: touch;
    background-color: rgb(246, 246, 246);
    /*font-size: 0.22rem*/
    position: relative;
}

span {
    display: inline-block;
}

address,
cite,
dfn,
em,
var {
    font-style: normal;
}


/* 重置列表元素 */

ul,
ol {
    list-style: none;
}


/* 重置文本格式元素 */

a {
    text-decoration: none;
    text-underline: none;
}

q:before,
q:after {
    content: '';
}


/* 重置表单元素 */

legend {
    color: #000;
}


/* for ie6 */

fieldset,
img {
    border: none;
    /*background-color: #eee;*/
    background-image: url("../img/no-img.png");
    background-repeat: no-repeat;
    background-position: 50%;
    background-size: 36%;
}


/* img 搭车：让链接里的 img 无边框 */


/* 注：optgroup 无法扶正 */

button,
input,
select,
textarea {
    outline: none;
    appearance: none;
    border: 0;
    /* 使得表单元素在 ie 下能继承字体大小 */
}


/* 重置表格元素 */

table {
    border-collapse: collapse;
    border-spacing: 0;
}


/* 重置 hr */

hr {
    border: none;
    height: 1px;
}


/**滚动条*/


/*定义滚动条高宽及背景 高宽分别对应横竖滚动条的尺寸*/

::-webkit-scrollbar {
    width: 0px;
    height: 0px;
    display: none;
}


/* 让非ie浏览器默认也显示垂直滚动条，防止因滚动条引起的闪烁 */

html {
    overflow-y: scroll;
}


/***路由过渡动画***/

.fade-enter-active,
.fade-leave-active {
    transition: opacity .1s ease;
}

.fade-enter,
.fade-leave-active {
    opacity: 0;
}


/**全局*/

.clearfix:after {
    display: block;
    clear: both;
    content: "";
    width: 100%;
    height: 0;
}

.textellipsisone {
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}

.textellipsis {
    display: -webkit-box;
    text-overflow: ellipsis;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
}

.p-container {
    height: 11.36rem;
    overflow: scroll;
}

.p-flex {
    display: flex;
    display: -webkit-flex;
    display: -webkit-box;
    display: box;
}

.p-flex-1 {
    flex: 1;
    -webkit-flex: 1;
    -webkit-box-flex: 1;
    box-flex: 1;
}

.p-flex-float {
    display: flex;
    flex-wrap: wrap;
    align-content: flex-start;
    -webkit-display: flex;
    -webkit-flex-wrap: wrap;
    -webkit-align-content: flex-start;
}

.p-align-justy {
    display: flex;
    display: -webkit-flex;
    justify-content: center;
    align-items: center
}


/*底部fixed-btn 商品详情，购物车*/

